import React from 'react'
import { useQuery, useMutation } from '@apollo/react-hooks'
import { GET_TODOS, DELETE_TODO, TOGGLE_TODO } from '../../queries'

import Note from '../components/Note'
import TodoForm from '../components/TodoForm.js'

const updateAfterDeleteMutation = (cache, { data }) => {
  ;(function(cache, data) {
    const { todoes } = cache.readQuery({ query: GET_TODOS })
    debugger
    cache.writeQuery({
      query: GET_TODOS,
      data: { todos: todos.filter(todo => todo.id !== data.deleteTodo.id) }
    })
  })(cache, data)
}

const App = () => {
  const { loading, error, data } = useQuery(GET_TODOS)
  const [deleteTodo] = useMutation(DELETE_TODO, { update: updateAfterDeleteMutation })
  const [toggleTodo] = useMutation(TOGGLE_TODO)
  if (loading) {
    return <div>We are loading yours todos, don't worry!</div>
  }
  if (error) {
    return <div>Oops! {error}</div>
  }
  return (
    <div className="container">
      <div>
        {data.todoes.map(todo => {
          return (
            <Note
              key={todo.id}
              text={todo.title}
              completed={todo.completed}
              deleteMethod={() => deleteTodo({ variables: { where: { id: todo.id } } })}
              toggleMethod={() =>
                toggleTodo({ variables: { data: { completed: !todo.completed }, where: { id: todo.id } } })
              }
            />
          )
        })}
      </div>
      <div>
        <TodoForm />
      </div>
    </div>
  )
}

export default App
